
























export default {
	data() {
		return {
			// 后台请求的验证码
			status: true,
			yzm: '',
			// 用户输入的信息
			tel: '',
			code: '',
			new_pass: ''
		};
	},
	methods: {
		// 获取用户输入的数据
		getText(e, name) {
			var that = this;
			if (name == 'tel') {
				that.tel = e.detail.value;
			} else if (name == 'code') {
				that.code = e.detail.value;
			} else if (name == 'new_pass') {
				that.new_pass = e.detail.value;
			}
		},
		// 获取验证码
		getYzm() {
			var that = this;
			var time = 60;
			var timer = null;
			if (that.status == true) {
				that.status = false;
				// 60s内不允许重复点击
				timer = setInterval(function() {
					if (time == 0) {
						that.status = !that.status;
						clearInterval(timer);
						timer = null;
					}
					time = time - 1;
				}, 1000);
				if (!(/^1[34578]\d{9}$/.test(that.tel))) {
					uni.showToast({
						title: '手机号格式不正确！',
						icon: 'none'
					})
				} else {
					uni.request({
						url: that.http + 'user/getTelCode',
						header: that.header,
						data: {
							tel: that.tel
						},
						method: 'POST',
						success: (res) => {
							console.log(res);
							if (res.data.code == 200) {
								uni.showToast({
									title: '发送成功，注意查收验证码！',
									icon: 'none'
								})
								that.yzm = res.data.data;
							}
						},
						fail() {
							uni.showToast({
								title: '网络延迟，请刷新后重试！',
								icon: 'none'
							})
						}
					})
				}
			}else {
				uni.showToast({
					title: '60s内请勿重复请求验证码！',
					icon: 'none'
				})
			}
		},
		// 提交注册信息
		submit(){
			var that = this;
			if(that.tel.length<3){
				uni.showToast({
					title:'手机号不能为空!',
					icon:'none'
				})
			}else if(that.code.length < 3){
				uni.showToast({
					title:'验证码不能为空!',
					icon:'none'
				})
			}else if(that.code != that.yzm){
				uni.showToast({
					title:'验证码错误!',
					icon:'none'
				})
			}else if(that.new_pass == 0 ){
				uni.showToast({
					title:'密码不能为空!',
					icon:'none'
				})			
			}else{
				uni.request({
					url:that.http+'user/forgetPass',
					header:that.header,
					data:{
						tel:that.tel,
						code:that.code,
						new_pass:that.new_pass
					},
					method:'POST',
					success: (res) => {
						console.log(res)
						console.log(that.tel)
						if(res.data.code==200){
							uni.showToast({
								title:res.data.data,
								icon:'none'
							})
							uni.navigateTo({
								url:'../login/login'
							})
						}else{
							uni.showToast({
								title:res.data.data,
								icon:'none'
							})
						}
					},
					fail(){
						uni.showToast({
							title:'网络延迟，请刷新后重试！',
							icon:'none'
						})
					}
				})
			}			
		}
	}
}
